/* App.css */

body {
  background-color: #F1F1F1;
  font-family: 'Ubuntu', sans-serif;
  margin: 0;
  color: #292929;
}

.container {
  background-color: #F1F1F1;
  padding: 20px;
  color: #292929;
}

.header {
  background-color: #F1F1F1;
  padding: 10px;
  text-align: center;
  font-weight: bold;
}

.logo {
  width: 500px;
}

.main {
  text-align: center;
  display: flex;
  justify-content: space-between;
}

.customer-form {
  flex: 1;
  margin-right: 20px;
}

.special-instructions-textarea {
  height: 100px;
  width: 300px;
}

.orders-list {
  flex: 1;
}

.button {
  background-color: limegreen;
  color: white;
  padding: 10px;
  border: none;
  cursor: pointer;
}
